import React, {useState} from 'react';
import { Link } from 'react-router-dom';
import * as FaIcons from "react-icons/fa";
import * as AiIcons from "react-icons/ai";
import {SideNavData} from './SideNavData';
import './Navbar.css';
import {IconContext} from 'react-icons';

export default function Navbar() {
    const [sidebar, setsidebar] = useState(false);
    const showSidebar = () => setsidebar(!sidebar);
    return (
        <>
        <IconContext.Provider value={{color: '#fff'}}>
        <div className="navbar">
            <Link to="" className="menu"><FaIcons.FaBars onClick={showSidebar}/></Link>
        </div>
        <nav className={sidebar ? "nav-menu active": "nav-menu"}>
            <ul className="nav-menu-items" onClick={showSidebar}>
                <li className="nav-menu-toggle"><Link to="" className="menu"><AiIcons.AiOutlineClose /></Link></li>
                 {SideNavData.map((item, index) =>{
                     return (
                         <li key={index} className={item.cName}>
                             <Link to={item.path} className="menu">{item.icon}<span>{item.title}</span></Link>
                         </li>
                     )
                 })}
            </ul>
          
        </nav>
        </IconContext.Provider>
        </>
    )
}